  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content-center {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.btn-yellow {
  background-color: #e08709;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-yellow-block {
  background-color: #e08709;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-yellow-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-green {
  background-color: #71b100;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-green:hover {
  background-color: #619800;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode .about-text blockquote {
  color: #e1e1e1;
}
.dark-mode .listing {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .listing .price {
  color: #71b100;
}
.dark-mode .listing .icon {
  filter: invert(100%);
}
.dark-mode .form label,
.dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #ffffff;
}

.no-webp .home {
  background-image: url(../img/header.jpg);
}

.webp .home {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.home {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.home .header-content {
    min-height: 70rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  margin: auto auto;
  color: #ffffff;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 0.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer p {
  margin: 0 auto;
}
.footer .navigation {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navigation {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.5s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 0;
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
    text-align: center;
    opacity: 1;
    visibility: visible;
  }
}
.navigation a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
}
.navigation .dark-mode-btn {
  display: block;
  width: 3rem;
  filter: invert(100%);
}
.navigation .dark-mode-btn:hover {
  cursor: pointer;
  filter: invert(0%);
}

.footer .navigation {
  justify-content: start;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .us-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .listing-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listing {
  background-color: #eeeeee;
  border: 1px solid #fbfbfb;
  margin-bottom: 2rem;
}
.listing .listing-content {
  padding: 2rem;
}
.listing h3,
.listing p {
  margin: 0;
}

.price {
  color: #71b100;
  font-weight: 700;
  font-size: 3rem;
}

.feature-icons {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.feature-icons li {
  flex: 1;
  display: flex;
}
.feature-icons li img {
  flex: 0 0 4rem;
  margin-right: 1rem;
}
.feature-icons li p {
  font-weight: 900;
}

.property-summary {
  display: grid;
  place-items: center;
}
.property-summary img {
  margin: 1rem;
}
.property-summary p {
  margin: 1rem;
}

.no-webp .image-contact {
  background-image: url("../img/encuentra.jpg");
}

.webp .image-contact {
  background-image: url("../img/encuentra.webp");
}

.image-contact {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.image-contact h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #ffffff;
}
.image-contact p {
  font-size: 1.8rem;
  color: #ffffff;
}

@media (min-width: 768px) {
  .lower-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-entry {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}

.entry-text a {
  color: 900;
}
.entry-text h4 {
  margin: 0;
  margin-top: 2rem;
  line-height: 1.4;
}
@media (min-width: 768px) {
  .entry-text h4 {
    margin-top: 0;
  }
}
.entry-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
  margin: 1rem;
}

.meta-info span {
  color: #e08709;
}

.testimonial {
  background-color: #71b100;
  border-radius: 2.5rem;
  font-size: 2.4rem;
  padding: 2rem;
  color: #ffffff;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #ffffff;
}

@media (min-width: 768px) {
  .about-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    justify-content: center;
  }
}

.about-content blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 2rem 0 1rem 0;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 15rem;
}
.form fieldset p {
  margin: 0;
  margin-bottom: 1rem;
}
.form .contact-options {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form .contact-options label {
  display: flex;
  align-items: center;
  justify-content: center;
}
.form .contact-options label p {
  font-weight: 700;
  font-size: 1.6rem;
  margin: 0 1.5rem;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem;
}
.alerta.error {
  background-color: #de4343;
}